.contact-wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 10vh;
    margin-bottom: 10vh;
    text-align: center;
}

.company-info {
    margin-top: 3vh;
    font-size: larger;
}

.opening-hours {
    margin-top: 3vh;
}

.closed-days {
    color: red;
}

@media (max-width: 480px) {
    .contact-wrapper {
        flex-direction: column;
        margin-top: 3vh;
        margin-bottom: 3vh;
    }

    .indopangan-company {
        margin-top: 3vh;
    }
}