.background-container {
    width: 100%; 
    height: 60vh; /* Full viewport height */
    background-image: url('../../img/bg-homepage.jpg'); /* Path to your image */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.about-us-container {
    display: flex;
    align-items: stretch;
    justify-content: center;
    margin-top: 5vh;
}

.company-logo-homepage {
    width: 40vh;
    height: 100%;
    display: flex;
    justify-content: center;
}

.summary-container {
    height: inherit;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
    margin-left: 2vw;
    margin-right: 2vw;
    align-items: center;
}

.title {
    font-weight: bold;
    font-size: x-large;
}

.sub-title {
    font-size: larger;
}

.description {
    margin-top: 3vh;
    font-size: larger;
}

.more-button {
    display: flex;
    flex-direction: column;
    margin-top: 3vh;
    width: 25vw;
    height: 5vh;
    border-radius: 12px;
    background-color: #a3803e;
    align-items: center;
    justify-content: center;
    color: #ffffff;
    cursor: pointer;
    font-size: larger;
}

.more-button:hover {
    background-color: #735724;
}

.products-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
    margin-top: 5vh;
    margin-bottom: 5vh;
}

.card-container {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: 3vh;
}

.card {
    width: 30vw;
    height: 60vh;
    border-radius: 30px;
    padding: 20px;
    margin: 10px;
    box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.25); /* Drop shadow */
    /* background-image: url('../../img/bg-homepage.jpg'); Path to your image */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-color: white;
}

.card-title {
    font-weight: bold;
    font-size: large;
    color: #a3803e;
    background-color: #ffffff;
    border-radius: 30px;
    border: 1px solid #a3803e;
    width: auto;
    height: 8%;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Adjust height for smaller screens */
@media (max-width: 480px) {
    .background-container {
        height: 40vh; /* 40% of the viewport height for small mobile screens */
    }

    .about-us-container {
        margin-top: 3vh;
        flex-direction: column; /* Stack items vertically on smaller screens */
        align-items: center; /* Center items horizontally */
    }

    .summary-container {
        margin-top: 3vh;
        margin-left: 0;
        margin-right: 0;
    }

    .description {
        margin-top: 2vh;
        padding-left: 7vw;
        padding-right: 7vw;
    }

    .company-logo-homepage {
        width: 30vh;
        height: 30vh;
    }

    .more-button {
        width: 45vw;
        height: 4vh;
    }

    .card-container {
        flex-direction: column;
    }

    .card {
        width: 90vw;
        height: 30vh;
    }

    .card-title {
        height: 12%;
    }
}