.footer-wrapper{
    display: flex;
}

.footer-information{
    width: 60%;
    align-self: center;
    font-size: medium;
    margin-right: 10%;
    margin-left: 10%;
}

.email-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.telephone-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-top: 25px;
    margin-bottom: 25px;
}

.address-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.email{
    font-weight: bold;
}
.telephone{
    font-weight: bold;
}
.address{
    font-weight: bold;
}

@media (max-width: 480px) {
    .footer-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
    }

    .footer-information {
        width: 100%;
        margin-right: 0;
        margin-left: 0;
        margin-top: 5vh;
    }

    .email-wrapper, .telephone-wrapper, .address-wrapper {
        display: flex;
        flex-direction: column;
    }
}