.layout {
    min-height: 100vh;
    position: relative;
    z-index: 1;
    overflow: hidden; /* Ensure pseudo-element covers the entire area */
  }
  
  .content-background {
    position: relative;
    z-index: 1;
    height: 100%; /* Ensure it covers the content area */
  }
  
  .content-background::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url('./img/content-bg-pngegg.png');
    /* background-image: url('./img/content-bg-lovepik.png'); */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0.1; /* Adjust opacity as needed */
    z-index: -1;
  }