.image-background {
    width: 100%; 
    height: 65vh; /* Full viewport height */
    background-image: url('../../img/bg-about-us2.jpg'); /* Path to your image */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.text-container{
    color: white;
    text-align: center;
    padding-top: 3vh;
    padding-left: 20vw;
    padding-right: 20vw;
}

.company-description, .goals {
    font-size: larger;
    margin-top: 5vh;
}

.vision-mission {
    padding-left: 5vw;
    padding-right: 5vw;
    padding-top: 5vh;
}

.vision-container {
    text-align: center;
}

.mission-container {
    margin-top: 5vh;
}

.vision-description{
    font-size: larger;
    margin-top: 5vh;
    padding-right: 20vw;
    padding-left: 20vw;
}

.mission-description {
    display: flex;
    justify-content: center;
    gap: 50px;
    align-items: center;
    margin-top: 5vh;
}

.about-us-title {
    font-weight: bold;
    font-size: x-large;
    display: flex;
    justify-content: center;
}

.core-values {
    margin-top: 5vh;
}

.value-container {
    display: flex;
    justify-content: center;
    gap: 50px;
    align-items: center;
    margin-top: 5vh;
}

.value-card {
    width: 250px;
    height: 200px;
    border-radius: 30px;
    padding: 20px;
    margin: 10px;
    box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.25); /* Drop shadow */
    text-align: center;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.value-card-core {
    width: 250px;
    height: 200px;
    border-radius: 30px;
    padding: 20px;
    margin: 10px;
    box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.25); /* Drop shadow */
    text-align: center;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.value-title {
    font-weight: bold;
    font-size: x-large;
    color: #c1914a;
    display: flex;
    justify-content: center;
}

.value-description {
    align-items: center;
    /* padding: 10px; */
    font-size: larger;
    justify-content: center;
}
.value-description-core {
    margin-top: 2rem;
    align-items: center;
    /* padding: 10px; */
    font-size: larger;
    justify-content: center;
}

.clients {
    margin-top: 5vh;
    margin-bottom: 5vh;
}

.client-container {
    margin-top: 5vh;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 50px;
}

.client-card {
    width: 250px;
    height: 250px;
    /* border-radius: 30px; */
    /* padding: 20px; */
    /* margin: 10px; */
    box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.25); /* Drop shadow */
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    background-color: white;
}

.client-img {
    width: 230px;
}

@media (max-width: 480px) {
    .image-background {
        height: 70vh;
    }

    .text-container {
        padding-left: 10vw;
        padding-right: 10vw;
    }

    .company-description, .goals {
        font-size: medium;
    }

    .client-container {
        overflow-y: scroll;
    }

    .mission-description, .value-container {
        flex-direction: column;
        gap: 5px;
    }

    .mission-container, .core-values, .clients {
        margin-top: 3vh;
    }

    .vision-description, .mission-description, .value-container, .client-container {
        margin-top: 1vh;
    }

    .client-container {
        display: flex;
        flex-direction: column;
        /* grid-template-columns: repeat(2, 1fr); */
        gap: 20px;
        /* margin-bottom: 5vh; */
    }

    .client-card {
        width: 250px;
        height: 250px;
        /* border-radius: 30px; */
        /* padding: 20px; */
        /* margin: 10px; */
        box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.25); /* Drop shadow */
        display: flex;
        justify-content: center;
        align-items: center;
        margin: auto;
    }
    
    .client-img {
        width: 230px;
    }

}