.product-wrapper {
    padding: 20px;
}

.product-container {
    padding: 20px;
}
  
.category-container {
    margin-bottom: 40px;
}

.title-category {
    text-align: center;
    font-size: x-large;
}

.product-list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
}

.product-card {
    width: 200px;
    /* border: 1px solid #ddd; */
    border-radius: 8px;
    overflow: hidden;
    text-align: center;
    box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.25);
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: white;
}

.product-image {
    width: 100%;
    height: 150px;
    object-fit: cover;
    border-bottom: 2px solid #000000;
}

.product-name {
    margin-top: 10px;
    font-weight: bold;
}

/* MODAL STYLING*/
.image-desc-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
}

.image-modal {
    width: 40%;
    height: 50%;
    background-size: cover;
}

.desc-modal {
    margin-left: 20px;
    margin-top: 0;
    margin-bottom: 0;
}

.spec-size-container {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.modal-title {
    font-size: larger;
    font-weight: bolder;
}